        opacity: 0
    }

    to {
        opacity: 1
    }
}

@keyframes modal-video {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@-webkit-keyframes modal-video-inner {
    0% {
        -webkit-transform: translateY(100px);
        transform: translateY(100px)
    }

    to {
        -webkit-transform: translate(0);
