.modal-video {
    -webkit-animation-duration: .3s;
    animation-duration: .3s;
    -webkit-animation-name: modal-video;
    animation-name: modal-video;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    background-color: rgba(0,0,0,.5);
    cursor: pointer;
    height: 100%;
    left: 0;
    opacity: 1;
    position: fixed;
    top: 0;
    transition: opacity .3s ease-out;
    width: 100%;
    z-index: 1000000
}

.modal-video-effect-exit {
    opacity: 0
}

.modal-video-effect-exit .modal-video-movie-wrap {
    -webkit-transform: translateY(100px);
    transform: translateY(100px)
}

.modal-video-body {
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 960px;
    padding: 0 10px
}

.modal-video-body,.modal-video-inner {
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%
}

.modal-video-inner {
    align-items: center
}

@media (orientation: landscape) {
    .modal-video-inner {
        box-sizing:border-box;
        padding: 10px 60px
    }
}

.modal-video-movie-wrap {
    -webkit-animation-duration: .3s;
    animation-duration: .3s;
    -webkit-animation-name: modal-video-inner;
    animation-name: modal-video-inner;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    background-color: #333;
    height: 0;
    padding-bottom: 56.25%;
    position: relative;
    -webkit-transform: translate(0);
    transform: translate(0);
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out,-webkit-transform .3s ease-out;
    width: 100%
}

.modal-video-movie-wrap iframe {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%
}

.modal-video-close-btn {
    background: transparent;
    border: none;
    display: inline-block;
    height: 35px;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: -45px;
    width: 35px;
    z-index: 2
}

@media (orientation: landscape) {
    .modal-video-close-btn {
        right:-45px;
        top: 0
    }
}

.modal-video-close-btn:before {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg)
}

.modal-video-close-btn:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg)
}

.modal-video-close-btn:after,.modal-video-close-btn:before {
    background: #fff;
    border-radius: 5px;
    content: "";
    height: 2px;
    left: 0;
    margin-top: -6px;
    position: absolute;
    top: 50%;
    width: 100%
}

#root>div>section.hero-area>div.container>div>div:first-child>div>ul>li:first-child>a>div:before {
    -webkit-animation: glowing 30s linear infinite;
    animation: glowing 30s linear infinite
}

.slide {
    -webkit-animation: moveToRight 1s ease-in-out;
    animation: moveToRight 1s ease-in-out;
    -webkit-animation-delay: 3.01s;
    animation-delay: 3.01s
}

.signupbutton {
    align-items: center;
    background: #fff;
    border: none;
    border-radius: 10px;
    color: #000;
    cursor: pointer;
    display: flex;
    height: 50px;
    justify-content: center;
    margin-top: 20px;
    outline: none;
    position: relative;
    transition: scale .3s ease-in-out;
    width: 220px;
    z-index: 0
}

.signupbutton:before {
    -webkit-animation: glowing 10s linear infinite;
    animation: glowing 10s linear infinite;
    background: linear-gradient(90deg,#f50000,#1400c8 20%,#ffd521 40%,#89216b 60%,#00ff56);
    background-size: 400%;
    -webkit-filter: blur(10px);
    filter: blur(10px);
    height: calc(100% + 4px);
    left: -2px;
    opacity: 1;
    top: -2px;
    transition: opacity .3s ease-in-out;
    width: calc(100% + 4px)
}

.signupbutton:after,.signupbutton:before {
    border-radius: 10px;
    content: "";
    position: absolute;
    z-index: -1
}

.signupbutton:after {
    background: #fff;
    height: 100%;
    left: 0;
    top: 0;
    width: 100%
}

.signupbutton:active {
    scale: .95
}

.signupbutton:active:before {
    opacity: 0
}

.signupbutton i {
    margin-right: 10px
}

@-webkit-keyframes glowing {
