.step {
    background: #fff;
    background: var(--not-attended-color);
    color: #000a38;
    font-family: Righteous;
    font-size: 1.075rem;
    font-weight: 400;
    height: 2.0625rem;
    line-height: 2rem;
    margin: .1em;
    position: relative;
    text-align: center;
    width: 2.0625rem
}

.step:not(:last-child) {
    margin-right: 1.875rem
}

.step:after {
    background: #fff;
    background: var(--not-attended-color);
    content: "";
    height: .1rem;
    left: 40px;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 1.2rem
}

.step:last-child {
    margin-right: 30px
}

.step:last-child:after {
    display: none
}

.step.active {
    opacity: 1
}

.step.active,.step.active:after {
    background: #ffbf00;
    background: var(--current-color)
}

.step.correct {
    color: #fff;
    opacity: 1
}

.step.correct,.step.correct:after {
    background: linear-gradient(315deg,#00b712,#5aff15 74%);
    background: var(--right-color)
}

.step.incorrect {
    color: #fff;
    opacity: 1
}

.step.incorrect,.step.incorrect:after {
    background: linear-gradient(147deg,#900,red 74%);
    background: var(--wrong-color)
}

.step.finish,.step.finish:after {
    background: #ffbf00;
    background: var(--current-color)
}

