.loader1 div {
    -webkit-animation: animate111 1.5s ease-in-out infinite;
    animation: animate111 1.5s ease-in-out infinite;
    background-color: #f2b900;
    border-radius: 50%;
    display: inline-block;
    height: 30px;
    margin: .5rem;
    -webkit-transform: scale(0);
    transform: scale(0);
    width: 30px
}

.loader1 div:nth-child(0) {
    -webkit-animation-delay: 0s;
    animation-delay: 0s
}

.loader1 div:first-child {
    -webkit-animation-delay: .2s;
    animation-delay: .2s
}

.loader1 div:nth-child(2) {
    -webkit-animation-delay: .4s;
    animation-delay: .4s
}

.loader1 div:nth-child(3) {
    -webkit-animation-delay: .6s;
    animation-delay: .6s
}

.loader1 div:nth-child(4) {
    -webkit-animation-delay: .8s;
    animation-delay: .8s
}

.loader1 div:nth-child(5) {
    -webkit-animation-delay: 1s;
    animation-delay: 1s
}

.loader1 div:nth-child(6) {
    -webkit-animation-delay: 1.2s;
    animation-delay: 1.2s
}

.loader1 div:nth-child(7) {
    -webkit-animation-delay: 1.4s;
    animation-delay: 1.4s
}

@-webkit-keyframes animate111 {
    0%,to {
        background-color: #b900b4;
        -webkit-transform: scale(.2);
        transform: scale(.2)
    }

    40% {
        background-color: #1400c8;
